.body{
    flex: 0.8;
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
    height: 100vh;
    color: white;
    padding: 30px;
}

.body__info{
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__infoText{
    flex: 1;
}

.body__info > img{
    height: 9vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, .5);
}

.body__infoText > h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.body__infoText > p{
    font-size: 14px;
}